@import "../libs/normalize.css";
html {
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
html {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}
html ul,
html li,
html h1,
html h2,
html h3,
html h4,
html h5,
html p {
  margin: 0;
  padding: 0;
}
.menu-bar {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  color: #666666;
}
.menu-bar .my-container {
  margin: 0 auto;
  display: flex;
}
.menu-bar .my-container .logo {
  padding: 30px 55px;
  cursor: pointer;
  border-bottom: 4px solid #69d2e700;
}
.menu-bar .my-container .logo:hover {
  border-bottom: 4px solid #69d2e7;
}
.menu-bar .my-container .navigation .menu {
  list-style: none;
  display: flex;
}
.menu-bar .my-container .navigation .menu .menu__item {
  padding: 40px 78px;
  border-right: 1px solid #404547;
  border-bottom: 4px solid #69d2e700;
}
.menu-bar .my-container .navigation .menu .menu__item a {
  text-decoration: none;
  color: #666666;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 400;
}
.menu-bar .my-container .navigation .menu .menu__item a:hover {
  color: #69d2e7;
}
.menu-bar .my-container .navigation .menu .menu__item i {
  cursor: pointer;
}
.menu-bar .my-container .navigation .menu .menu__item i:hover {
  color: #69d2e7;
}
.menu-bar .my-container .navigation .menu .menu__item:first-child {
  border-left: 1px solid #404547;
}
.menu-bar .my-container .navigation .menu .menu__item:hover {
  border-bottom: 4px solid #69d2e7;
}
.menu-bar .my-container .navigation .menu .menu__item:last-child {
  padding: 40px 40px 0 149px;
  border-right: none;
  border-bottom: none;
}
.header {
  min-height: 990px;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  background-image: url(../../build/img/backgrounds/0-header.png);
  background-size: cover;
  background-position: center;
  position: relative;
}
.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #12181c;
  opacity: 0.7;
}
.header .my-container {
  padding-top: 439px;
  z-index: 1;
  width: 1030px;
  margin: 0 auto;
  opacity: 0.8;
}
.header .my-container .title {
  text-align: center;
  margin-bottom: 274px;
}
.header .my-container .title .name {
  font-family: 'Arimo', sans-serif;
  color: #ffffff;
  font-size: 75px;
  font-weight: 900;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 25px;
}
.header .my-container .title .description {
  letter-spacing: 8px;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 49px;
}
.header .my-container .title .btn {
  font-family: 'Montserrat', sans-serif;
  padding: 20px 42px;
  transition: 0.7s;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  border: 1px solid #ffffff;
  cursor: pointer;
}
.header .my-container .title .btn-empty {
  background: transparent;
  border: 1px solid #ffffff;
}
.header .my-container .title .btn-empty:hover {
  transition: 0.7s;
  background: linear-gradient(to right, #69d2e7 0%, #817bff 100%);
}
.header .my-container .pagination {
  display: flex;
  justify-content: center;
  text-decoration: none;
  text-align: center;
}
.header .my-container .pagination .pagination__btn {
  cursor: pointer;
  width: 14px;
  height: 14px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  background-color: #ffffff;
  opacity: 0.5;
  margin-right: 10px;
}
.header .my-container .pagination .pagination__btn:hover {
  opacity: 1;
}
.about-cesis {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  /*     background-color: green; */
  text-align: center;
  padding-top: 150px;
  padding-bottom: 150px;
}
.about-cesis .container {
  /*         background-color: yellow; */
}
.about-cesis .container .about-cesis__text {
  color: #95999c;
  font-family: 'Merriweather', serif;
  font-size: 26px;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 143px;
}
.about-cesis .container .about-cesis-menu {
  margin-bottom: 121px;
  list-style: none;
  text-align: center;
  justify-content: space-between;
}
.about-cesis .container .about-cesis-menu .about-cesis-menu__item {
  color: #999999;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  padding-bottom: 17px;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  letter-spacing: 0px;
}
.about-cesis .container .about-cesis-menu .about-cesis-menu__item:hover {
  color: #69d2e7;
  border-bottom: 2px solid #69d2e7;
}
.about-cesis .container .about-cesis-menu .about-cesis-menu__item:active {
  color: #333333;
  border-bottom: 2px solid #333333;
}
.about-cesis .container .about-cesic-wrapper {
  /*             background-color: red; */
  margin-bottom: 95px;
  cursor: pointer;
}
.about-cesis .container .about-cesic-wrapper .about-cesic__icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #e0e0e0;
  text-align: center;
  line-height: 105px;
  margin-right: 30px;
}
.about-cesis .container .about-cesic-wrapper .about-cesic__icon .icon-awesome {
  background-image: linear-gradient(to right, #69d2e7 0%, #817bff 100%);
  color: transparent;
  background-clip: text;
  font-size: 24px;
}
.about-cesis .container .about-cesic-wrapper .block .about-cesic__name {
  text-align: left;
  color: #333333;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 3.2px;
}
.about-cesis .container .about-cesic-wrapper .block .about-cesic__description {
  text-align: left;
  color: #666666;
  font-family: "Open Sans";
  font-size: 12px;
  font-weight: 400;
  line-height: 30.02px;
  overflow: hidden;
}
.about-cesis .container .about-cesic-wrapper:hover .about-cesic__icon {
  box-shadow: 0 0 21px rgba(116, 124, 141, 0.31);
}
.about-cesis .container .about-cesic-wrapper:hover .block .about-cesic__name {
  color: #69d2e7;
}
